var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"info base_width"},[_c('h3',{staticClass:"header font-weight-normal"},[_vm._v("恒大蝉联“2020中国房地产百强”综合实力TOP10第一")]),_c('h5',{staticClass:"tips font-weight-normal"},[_vm._v("发布时间：2020-03-18 内容来源：中国网")]),_c('div',{staticClass:"content"},[_c('div',{staticClass:"box-header"},[_c('p',{staticClass:"indent"},[_vm._v("3月17日，由中国企业评价协会、清华大学房地产研究所和中指研究院三家研究机构共同组成的“中国房地产TOP10研究组”，在京正式发布“2020中国房地产百强”名单，恒大位居“中国房地产百强企业综合实力TOP10”榜单第一名，这已是恒大连续四年蝉联榜首。")]),_c('div',{staticClass:"hd_news_company_info"},[_c('div',{staticClass:"hd_new_company_info_img bg_cover",style:({width: '514px',height:'740px',backgroundImage: ("url(" + (require('../../assets/images/news/news14.png')) + ")")})})]),_c('p',{staticClass:"indent"},[_vm._v("在同日公布的分榜单中，恒大还夺得三项第一：“2020中国房地产百强企业——融资能力TOP10”第一、“2020中国房地产百强企业——运营效率TOP10”第一、并蝉联“2019-2020中国房地产年度扶贫标杆企业”第一。")]),_c('p',{staticClass:"indent"},[_vm._v("分析认为，恒大自2017年起向“规模+效益”发展模式转变，主动调整经营模式，在激烈的市场竞争中实现高质量增长，彰显企业强劲实力，这是其能连续多年蝉联房企综合实力榜首的重要原因")]),_c('p',{staticClass:"indent"},[_vm._v("数据显示，2019年恒大实现合约销售额6010.6亿元，销售面积5846.3万平方米，完成年度销售目标，继续保持稳健增长；2020年，恒大目标合同销售金额6500亿。")]),_c('p',{staticClass:"indent"},[_vm._v("2020年初，在楼市一片暗淡的环境下，恒大以大开大合的营销手段准确出击，全面开启颠覆性的网上卖房模式，通过一系列让利组合拳，最大力度刺激楼市，成功实现了逆势飘红。")]),_c('p',{staticClass:"indent"},[_vm._v("数据显示，恒大于2020年2月13日全面启动网上卖房，截至2月底共实现网上认购99141套，认购金额1026.7亿。受网上卖房利好带动，恒大2月实现销售额447.3亿，销售面积513.8万平方米，分别同比大增108%及154%，均居行业首位。截至2月末，恒大累计销售853亿，排在行业第一。")]),_c('p',{staticClass:"indent"},[_vm._v("进入3月，恒大再度胜追击，通过降门槛、升福利等举措大幅提升让利力度，进一步点燃市场购买力。")]),_c('p',{staticClass:"indent"},[_vm._v("根据恒大政策，客户只要缴纳3000元定金就能锁定一套房源，之后如若本人购买或推荐成交任意购买一套恒大房源，均能享受各项重大权益和奖励，其中推荐成交即可赚3.5万现金奖励及1%佣金，“稳赚不赔”的模式，成功吸引广大消费者参与，打开线上领域蓝海。")]),_c('p',{staticClass:"indent"},[_vm._v("超强折扣力度，也是恒大网上卖房的一大亮点。除了78折及恒房通推荐97折，恒大还针对楼宇去化率、不同付款方式给出多重折扣，尽最大幅度让利购房者。外加“最低价购房”和“无理由推退房”两大兜底政策，打消购房者“怕买贵、怕买错”的顾虑。此外，购房者在网上认购后，自购或推荐均可换房。")]),_c('p',{staticClass:"indent"},[_vm._v("业内人士认为，恒大在网上卖房持续火热的情况下，依然不断升级其营销举措、作出更大的让利，这将进一步巩固恒大网上卖房“能买就买、能卖就卖”的良好局面，带动业绩实现快速攀升。")])])]),_c('infoPage',{attrs:{"first":_vm.first,"last":_vm.last,"prevUrl":_vm.prevUrl,"nextUrl":_vm.nextUrl}})],1)}
